<template>
	<view class="activity-pick">
		<view class="form-card">
			<view class="form-item flex-row border-bottom">
				<text>取件时间</text>
				<picker mode="date" :value="getDate(formData.time, '-')" @change="bindDateChange">
					<view class="flex-row">
						<view class="add-time">{{getDate(formData.time, '.')}}</view>
						<uni-icons type="arrowright" size="16" color="#5D5D5D" style="margin-right: -6rpx;"></uni-icons>
					</view>
				</picker>
			</view>
			<view class="form-item flex-row border-bottom">
				<text>取件地址</text>
				<uni-icons type="location-filled" size="20" color="#5D5D5D" style="margin-right: -6rpx;"></uni-icons>
			</view>
			<view class="form-item flex-row border-bottom">
				<text>联系人</text>
				<text>赚钱小煮妇</text>
			</view>
			<view class="form-item flex-row">
				<text>联系电话</text>
				<text>17648890021</text>
			</view>
		</view>
		<view class="btn-bottom flex-col" @click="submit()">
			<text>确认预约</text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				formData: {
					time: new Date()
				}
			};
		},
		methods: {
			bindDateChange(e) {
				let {
					value
				} = e.detail
				this.formData.time = new Date(value)
			},
			getDate(date, type) {
				// console.log(date)
				if (date instanceof Date) {
					let y = date.getFullYear()
					let m = date.getMonth() + 1;
					m = m > 9 ? m : '0' + m
					let d = date.getDate();
					d = d > 9 ? d : '0' + m
					if (!type) {
						return `${y}年${m}月${d}日`
					} else {
						return `${y}${type}${m}${type}${d}`
					}
				} else {
					return date
				}
			},
		}
	}
</script>

<style lang="scss" scoped>
	.activity-pick {
		padding: 16rpx 24rpx 180rpx;

		.form-card {
			background-color: #fff;
			box-shadow: 0 8rpx 20rpx 0 rgba(0, 0, 0, 0.02);
			border-radius: 10rpx;
			padding: 0 41rpx;
			font-size: 30rpx;
			line-height: 42rpx;

			.form-item {
				padding: 41rpx 0;
				justify-content: space-between;

			}
		}

		.btn-bottom {
			position: fixed;
			bottom: 60rpx;
			left: 24rpx;
			right: 24rpx;
			height: 90rpx;
			background: #FFD44D;
			box-shadow: 0 6rpx 12rpx 0 rgba(255, 212, 77, 0.2);
			border-radius: 45rpx;
			font-size: 30rpx;
			line-height: 42rpx;
		}
	}
</style>
